.categories {
  ul {
    display: flex;

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}

// MEDIA 

@media (max-width: 1400px) {
  .categories {
    margin-bottom: 20px;
  }
}

@media (max-width: 1150px) {
  .categories {
    width: 500px;
    overflow: auto;
    ul {
      width: 785px;
    }
  }
}
